@import "../plugins/font_awesome/fontawesome.scss";
@import "../plugins/font_awesome/solid.scss";
@import "../plugins/font_awesome/brands.scss";
@import "../plugins/font_awesome/regular.scss";

/* ========== Menu ========== */

.stretchynav {
	position: fixed;
	z-index: 5;
	top: 18px;
	right: 3%;
	pointer-events: none;
	@extend %noHighlightAndSelect;
	&.nav-is-visible {
		pointer-events: auto;
	}
	&.nav-is-visible .stretchynav_bg {
		height: 100%;
		box-shadow: 0 6px 15px 8px rgba(#000, .5);
	}
}
.stretchynav_bg {
		--stretchynav_background: transparent;
		/* this is the stretching navigation background */
		position: absolute;
		z-index: 1;
		top: 0;
		right: 0;
		width: 60px;
		height: 60px;
		border-radius: 30px;
		background: var(--stretchynav_background);
		box-shadow: 0 1px 15px 8px rgba(#000, .5);
		transition: background 2s, height .2s, box-shadow 1s;
}
.stretchynav_trigger {
	position: absolute;
	z-index: 3;
	top: 0;
	right: 0;
	height: 60px;
	width: 60px;
	border-radius: 50%;
	/* replace text with image */
	overflow: hidden;
	white-space: nowrap;
	color: transparent;
	pointer-events: auto;
	span, span::after, span::before {
		/* this is the hamburger icon */
		position: absolute;
		width: 16px;
		height: 2px;
		background-color: $white;
	}
	span {
		/* middle line of the hamburger icon */
		@include center;
		transition: background-color .2s;
	}
	span::after, span::before {
		/* top and bottom lines of the hamburger icon */
		content: '';
		top: 0;
		left: 0;
		backface-visibility: hidden;
		transition: transform .2s;
	}
	span::before {
		transform: translateY(-6px);
	}

	span::after {
		transform: translateY(6px);
	}
	.no-touch &:hover ~ .stretchynav_bg {
		box-shadow: 0 6px 30px rgba(#000, .2);
	}
	.nav-is-visible & {
		span {
			background-color: transparent;
		}

		span::before {
			transform: rotate(-45deg);
		}

		span::after {
			transform: rotate(45deg);
		}
	}
}	
.stretchynav ul {
	position: relative;
	z-index: 2;
	padding: 30px 0 0;
	visibility: hidden;
	transition: visibility .3s;
	text-align: right;
	list-style-type: none;
	.a_item {
		position: relative;
		display: block;
		height: 50px;
		line-height: 50px;
		padding: 0 calc(1em + 60px) 0 1em;
		color: rgba($white, .6);
		font-size: 1.8rem;
		transition: color .2s;

		&::after {
			/* navigation item icons */
			position: absolute;
			font-size: 20px;
			height: 20px;
			width: 20px;
			right: 20px;
			top: 50%;
			transform: translateY(-50%) scale(0);
			opacity: .6;
			@include fa-icon;
			@extend .fas;
		}
		&::before {
			/* line visible next to the active navigation item */
			content: '';
			position: absolute;
			width: 3px;
			height: 16px;
			top: 50%;
			right: 60px;
			transform: translateX(3px) translateY(-50%) scaleY(0);
			background-color: var(--text_color);
		}
	}
	li:first-of-type .a_item::after {
		content: fa-content($fa-var-user-tie);
		padding-right: 1px;
	}

	li:nth-of-type(2) .a_item::after {
		content: fa-content($fa-var-layer-group);
	}

	li:nth-of-type(3) .a_item::after {
		content: fa-content($fa-var-tasks);

	}
	li:nth-of-type(4) .a_item::after {
		content: fa-content($fa-var-mobile-alt);
		padding-right: 3px;
	}
	span {
		/* navigation item labels */
		display: block;
		opacity: 0;
		transform: translateX(-25px);
	}
}

.stretchynav.nav-is-visible ul {
	visibility: visible;
	.a_item::after {
		/* navigation item icons */
		transform: translateY(-50%) scale(1);
		animation: scaleIn .15s backwards;
		transition: opacity .2s;
	}
	.a_item .active {
		color: $white;
		&::after {
			opacity: 1;
		}
		&::before {
			transform: translateX(3px) translateY(-50%) scaleY(1);
			transition: transform .15s .3s;
		}
	}
	span {
		opacity: 1;
		transform: translateX(0);
		animation: slideIn .15s backwards;
		transition: transform .2s;
	}
	.no-touch & .a_item:hover {
		color: $white;
		&::after {
			opacity: 1;
		}
		span {
			transform: translateX(-5px);
		}
	}
	li:first-of-type .a_item::after, 
	li:first-of-type span {
		animation-delay: .05s;
	}

	li:nth-of-type(2) .a_item::after, 
	li:nth-of-type(2) span {
		animation-delay: .1s;
	}

	li:nth-of-type(3) .a_item::after, 
	li:nth-of-type(3) span {
		animation-delay: .15s;
	}

	li:nth-of-type(4) .a_item::after, 
	li:nth-of-type(4) span {
		animation-delay: .2s;
	}

	li:nth-of-type(5) .a_item::after, 
	li:nth-of-type(5) span {
		animation-delay: .25s;
	}

}

/* =========== keyframes ========== */

@keyframes scaleIn {
  from {
    transform: translateY(-50%) scale(0);
  }
  to {
    transform: translateY(-50%) scale(1);
  }
}

@keyframes slideIn {
  from {
   	opacity: 0;
	transform: translateX(-25px);
  }
  to {
    opacity: 1;
	transform: translateX(0);
  }
}

